"use client";

import { Dossier } from "@/lib/types";
import { Button } from "@/components/ui/button";
import { formatDate, normalizeText } from "@/lib/dossierUtils";

interface DossierListProps {
  dossiers: Dossier[];
  onSelect: (dossier: Dossier) => void;
  onDelete: (id: number) => void;
}

export function DossierList({ dossiers, onSelect, onDelete }: DossierListProps) {
  if (dossiers.length === 0) {
    return <p className="text-center p-4">Aucun dossier trouvé.</p>;
  }

  return (
    <div className="max-w-[400px] mx-auto space-y-4 p-4">
      {dossiers.map((dossier) => (
        <div
          key={dossier.id}
          className="border rounded p-4 shadow-sm flex justify-between items-center"
        >
          <div
            className="cursor-pointer"
            onClick={() => onSelect(dossier)}
            role="button"
            tabIndex={0}
            onKeyDown={(e) => {
              if (e.key === "Enter") onSelect(dossier);
            }}
          >
            <p className="font-semibold">
              {normalizeText(dossier.name)}
            </p>
            <p className="text-sm text-gray-600">
              Arrivée: {formatDate(dossier.arrivalDate)}
            </p>
          </div>
          <Button
            variant="destructive"
            size="sm"
            onClick={() => {
              if (confirm(`Supprimer le dossier "${normalizeText(dossier.name)}" ?`)) {
                onDelete(dossier.id);
              }
            }}
          >
            Supprimer
          </Button>
        </div>
      ))}
    </div>
  );
}
