import { Dossier } from "./types";

// Remove accents and normalize text
export const normalizeText = (text: string): string => {
  if (!text) return "";
  const normalized = text
    .normalize("NFD")
    .replace(/[\u0300-\u036f]/g, "")
    .toLowerCase();
  return normalized.charAt(0).toUpperCase() + normalized.slice(1);
};

// Format date to abbreviated format (DD/MM/YY)
export const formatDate = (date: string): string => {
  return new Date(date).toLocaleDateString("fr-FR", {
    day: "2-digit",
    month: "2-digit",
    year: "2-digit",
  });
};

// Generate automatic dossier number
export const generateDossierId = (existingDossiers: Dossier[]): number => {
  if (existingDossiers.length === 0) return 1;
  return Math.max(...existingDossiers.map((d) => d.id)) + 1;
};

// Validate dossier name (first letter uppercase, rest lowercase, no accents)
export const validateDossierName = (name: string): boolean => {
  const normalized = normalizeText(name);
  return (
    normalized === name && // Name should already be normalized
    /^[A-Z][a-z0-9\s-]*$/.test(name) // Starts with uppercase, followed by lowercase/numbers/spaces/hyphens
  );
};

// Local Storage Operations
const STORAGE_KEY = "la-gestiona-dossiers";

export const saveDossiers = (dossiers: Dossier[]): void => {
  try {
    localStorage.setItem(STORAGE_KEY, JSON.stringify(dossiers));
  } catch (error) {
    console.error("Error saving dossiers:", error);
    throw new Error("Failed to save dossiers");
  }
};

export const loadDossiers = (): Dossier[] => {
  try {
    const data = localStorage.getItem(STORAGE_KEY);
    return data ? JSON.parse(data) : [];
  } catch (error) {
    console.error("Error loading dossiers:", error);
    return [];
  }
};

// Search and filter functions
export const searchDossiers = (
  dossiers: Dossier[],
  query: string,
  startDate?: string,
  endDate?: string
): Dossier[] => {
  return dossiers.filter((dossier) => {
    const matchesName = dossier.name
      .toLowerCase()
      .includes(query.toLowerCase());
    
    if (startDate && endDate) {
      const dossierDate = new Date(dossier.arrivalDate);
      const start = new Date(startDate);
      const end = new Date(endDate);
      return matchesName && dossierDate >= start && dossierDate <= end;
    }
    
    return matchesName;
  });
};

// Calculate total entries
export const calculateTotalEntries = (dossiers: Dossier[]): number => {
  return dossiers.reduce((total, dossier) => total + (dossier.entriesCount || 0), 0);
};

// Group dossiers by name and calculate total entries
export const groupDossiersByName = (dossiers: Dossier[]): { 
  name: string; 
  totalEntries: number; 
}[] => {
  const groups = dossiers.reduce((acc, dossier) => {
    const key = dossier.name;
    if (!acc[key]) {
      acc[key] = { name: key, totalEntries: 0 };
    }
    acc[key].totalEntries += dossier.entriesCount || 0;
    return acc;
  }, {} as Record<string, { name: string; totalEntries: number }>);

  return Object.values(groups);
};
