"use client";

import { useState, useEffect } from "react";
import { Dossier } from "@/lib/types";
import { NavBar } from "@/components/navigation/NavBar";
import { DossierForm } from "@/components/dossier/DossierForm";
import { DossierList } from "@/components/dossier/DossierList";
import { loadDossiers, saveDossiers, generateDossierId } from "@/lib/dossierUtils";

export default function LaGestionaApp() {
  const [activeSection, setActiveSection] = useState<string>("ajt");
  const [dossiers, setDossiers] = useState<Dossier[]>([]);
  const [selectedDossier, setSelectedDossier] = useState<Dossier | null>(null);

  useEffect(() => {
    const storedDossiers = loadDossiers();
    setDossiers(storedDossiers);
  }, []);

  const addDossier = (dossier: Dossier) => {
    const newDossier = { ...dossier, id: generateDossierId(dossiers) };
    const updated = [...dossiers, newDossier];
    setDossiers(updated);
    saveDossiers(updated);
    setActiveSection("dos");
  };

  const updateDossier = (updatedDossier: Dossier) => {
    const updated = dossiers.map((d) => (d.id === updatedDossier.id ? updatedDossier : d));
    setDossiers(updated);
    saveDossiers(updated);
    setSelectedDossier(null);
    setActiveSection("dos");
  };

  const deleteDossier = (id: number) => {
    const updated = dossiers.filter((d) => d.id !== id);
    setDossiers(updated);
    saveDossiers(updated);
    if (selectedDossier?.id === id) {
      setSelectedDossier(null);
    }
  };

  const handleSelect = (dossier: Dossier) => {
    setSelectedDossier(dossier);
    setActiveSection("mod");
  };

  return (
    <div className="max-w-[400px] mx-auto min-h-screen bg-white">
      <NavBar activeSection={activeSection} onSectionChange={setActiveSection} />

      <div className="pt-16">
        {activeSection === "ajt" && <DossierForm onSubmit={addDossier} />}
        {activeSection === "dos" && (
          <DossierList dossiers={dossiers} onSelect={handleSelect} onDelete={deleteDossier} />
        )}
        {activeSection === "mod" && selectedDossier && (
          <DossierForm
            initialData={selectedDossier}
            onSubmit={updateDossier}
            onCancel={() => setActiveSection("dos")}
          />
        )}
        {/* Other sections (supprimer, états, rechercher) to be implemented */}
      </div>
    </div>
  );
}
