"use client";

import { useState } from "react";
import { Dossier, DossierStatus, EnteredDocs, MissingDocs } from "@/lib/types";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Checkbox } from "@/components/ui/checkbox";
import { Textarea } from "@/components/ui/textarea";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { normalizeText, validateDossierName } from "@/lib/dossierUtils";

interface DossierFormProps {
  initialData?: Dossier;
  onSubmit: (dossier: Dossier) => void;
  onCancel?: () => void;
}

export function DossierForm({ initialData, onSubmit, onCancel }: DossierFormProps) {
  const defaultFormData: Dossier = {
    id: initialData?.id || 0,
    name: "",
    arrivalDate: new Date().toISOString().split("T")[0],
    status: "En cours",
    enteredDocs: {
      purchases: false,
      sales: false,
      bank: false,
      cash: false,
      od: false,
    },
    missingDocs: {
      purchases: false,
      sales: false,
      statement: false,
      salaries: false,
      others: false,
      othersDetail: "",
    },
    remarks: "",
  };

  const [formData, setFormData] = useState<Dossier>(
    initialData ? { ...defaultFormData, ...initialData } : defaultFormData
  );

  const [errors, setErrors] = useState<Record<string, string>>({});

  const validateForm = (): boolean => {
    const newErrors: Record<string, string> = {};

    if (!formData.name) {
      newErrors.name = "Le nom est obligatoire";
    } else if (!validateDossierName(formData.name)) {
      newErrors.name = "Le nom doit commencer par une majuscule, sans accents";
    }

    if (!formData.arrivalDate) {
      newErrors.arrivalDate = "La date d'arrivée est obligatoire";
    }

    if (formData.status === "Terminé") {
      if (!formData.completionDate) {
        newErrors.completionDate = "La date de fin est obligatoire pour un dossier terminé";
      }
      if (!formData.entriesCount) {
        newErrors.entriesCount = "Le nombre d'écritures est obligatoire pour un dossier terminé";
      }
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!validateForm()) return;

    const normalizedName = normalizeText(formData.name || "");
    
    const dossier: Dossier = {
      id: initialData?.id || 0,
      name: normalizedName,
      arrivalDate: formData.arrivalDate || new Date().toISOString().split("T")[0],
      completionDate: formData.completionDate,
      status: formData.status as DossierStatus,
      entriesCount: formData.entriesCount,
      enteredDocs: formData.enteredDocs || {
        purchases: false,
        sales: false,
        bank: false,
        cash: false,
        od: false,
      },
      missingDocs: formData.missingDocs || {
        purchases: false,
        sales: false,
        statement: false,
        salaries: false,
        others: false,
        othersDetail: formData.missingDocs?.othersDetail,
      },
      remarks: formData.remarks,
    };

    onSubmit(dossier);
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-4 max-w-[400px] mx-auto p-4">
      <div className="space-y-2">
        <Label htmlFor="name">Nom du dossier</Label>
        <Input
          id="name"
          value={formData.name || ""}
          onChange={(e) => setFormData({ ...formData, name: e.target.value })}
          className={errors.name ? "border-red-500" : ""}
        />
        {errors.name && <p className="text-red-500 text-sm">{errors.name}</p>}
      </div>

      <div className="space-y-2">
        <Label htmlFor="arrivalDate">Date d'arrivée</Label>
        <Input
          id="arrivalDate"
          type="date"
          value={formData.arrivalDate || ""}
          onChange={(e) => setFormData({ ...formData, arrivalDate: e.target.value })}
          className={errors.arrivalDate ? "border-red-500" : ""}
        />
        {errors.arrivalDate && <p className="text-red-500 text-sm">{errors.arrivalDate}</p>}
      </div>

      <div className="space-y-2">
        <Label htmlFor="status">État du dossier</Label>
        <Select
          value={formData.status}
          onValueChange={(value) => setFormData({ ...formData, status: value as DossierStatus })}
        >
          <SelectTrigger>
            <SelectValue placeholder="Sélectionner l'état" />
          </SelectTrigger>
          <SelectContent>
            <SelectItem value="En cours">En cours</SelectItem>
            <SelectItem value="En attente">En attente</SelectItem>
            <SelectItem value="Terminé">Terminé</SelectItem>
          </SelectContent>
        </Select>
      </div>

      {formData.status === "Terminé" && (
        <>
          <div className="space-y-2">
            <Label htmlFor="completionDate">Date de fin de traitement</Label>
            <Input
              id="completionDate"
              type="date"
              value={formData.completionDate || ""}
              onChange={(e) => setFormData({ ...formData, completionDate: e.target.value })}
              className={errors.completionDate ? "border-red-500" : ""}
            />
            {errors.completionDate && (
              <p className="text-red-500 text-sm">{errors.completionDate}</p>
            )}
          </div>

          <div className="space-y-2">
            <Label htmlFor="entriesCount">Nombre d'écritures</Label>
            <Input
              id="entriesCount"
              type="number"
              min="0"
              value={formData.entriesCount || ""}
              onChange={(e) => setFormData({ ...formData, entriesCount: parseInt(e.target.value) })}
              className={errors.entriesCount ? "border-red-500" : ""}
            />
            {errors.entriesCount && (
              <p className="text-red-500 text-sm">{errors.entriesCount}</p>
            )}
          </div>
        </>
      )}

      <div className="space-y-2">
        <Label>Pièces saisies</Label>
        <div className="grid grid-cols-2 gap-4">
          {Object.entries({
            purchases: "Achats",
            sales: "Ventes",
            bank: "Banque",
            cash: "Caisse",
            od: "OD",
          }).map(([key, label]) => (
            <div key={key} className="flex items-center space-x-2">
              <Checkbox
                id={`entered-${key}`}
                checked={formData.enteredDocs[key as keyof EnteredDocs]}
                onCheckedChange={(checked) => {
                  setFormData((prev) => ({
                    ...prev,
                    enteredDocs: {
                      ...prev.enteredDocs,
                      [key as keyof EnteredDocs]: Boolean(checked),
                    },
                  }));
                }}
              />
              <Label htmlFor={`entered-${key}`}>{label}</Label>
            </div>
          ))}
        </div>
      </div>

      <div className="space-y-2">
        <Label>Pièces manquantes</Label>
        <div className="grid grid-cols-2 gap-4">
          {Object.entries({
            purchases: "Achats",
            sales: "Ventes",
            statement: "Relevé",
            salaries: "Salaires",
            others: "Autres",
          }).map(([key, label]) => (
            <div key={key} className="flex items-center space-x-2">
              <Checkbox
                id={`missing-${key}`}
                checked={formData.missingDocs[key as keyof MissingDocs]}
                onCheckedChange={(checked) => {
                  setFormData((prev) => ({
                    ...prev,
                    missingDocs: {
                      ...prev.missingDocs,
                      [key as keyof MissingDocs]: Boolean(checked),
                    },
                  }));
                }}
              />
              <Label htmlFor={`missing-${key}`}>{label}</Label>
            </div>
          ))}
        </div>
      </div>

      {formData.missingDocs?.others && (
        <div className="space-y-2">
          <Label htmlFor="othersDetail">Préciser autres pièces manquantes</Label>
          <Input
            id="othersDetail"
            value={formData.missingDocs.othersDetail || ""}
            onChange={(e) =>
              setFormData({
                ...formData,
                missingDocs: {
                  ...formData.missingDocs,
                  othersDetail: e.target.value,
                },
              })
            }
          />
        </div>
      )}

      <div className="space-y-2">
        <Label htmlFor="remarks">Remarques</Label>
        <Textarea
          id="remarks"
          value={formData.remarks || ""}
          onChange={(e) => setFormData({ ...formData, remarks: e.target.value })}
        />
      </div>

      <div className="flex justify-end space-x-2 pt-4">
        {onCancel && (
          <Button type="button" variant="outline" onClick={onCancel}>
            Annuler
          </Button>
        )}
        <Button type="submit">
          {initialData ? "Modifier" : "Ajouter"}
        </Button>
      </div>
    </form>
  );
}
